import { Fragment, useCallback, useEffect, useState } from "react";
import { Helmet } from "react-helmet-async";
import { IconButton } from "@mui/material";

import WAUsageApi from "Apis/WAUsageApi";
import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import { AppLink } from "Components/Common";
import {
  ContentHeader,
  ContentInner,
  ContentWrapper,
  ContentWrapperSX,
  HeaderTitle,
} from "Containers/BusinessSettings/LayoutComponents";
import { LeftArrow } from "Assets/Icons";
import LiveStatus from "./WAUsageReports/LiveStatus";
import OffStatus from "./WAUsageReports/OffStatus";
import UtilizationReport from "./WAUsageReports/UtilizationReport";
import Utilization from "./WAUsageReports/Utilization";

const WAUsageReports = () => {
  const { isMobile } = useGlobal();
  const { setErrorMessage } = useSnacks();
  const [loadingStatus, setLoadingStatus] = useState<boolean>(true);
  const [waStatus, setWaStatus] = useState<boolean>(false);
  const getWaStatus = useCallback(async () => {
    try {
      const res = await WAUsageApi.getWaStatus();
      if (!res.success) {
        throw Error(res.message);
      }
      setWaStatus(res.success);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setLoadingStatus(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    getWaStatus();
  }, [getWaStatus]);

  return (
    <Fragment>
      <Helmet>
        <title>Usage and Reports</title>
      </Helmet>
      {isMobile && (
        <ContentHeader>
          <IconButton
            component={AppLink}
            to="/business-settings"
            sx={{ width: 32, height: 32 }}
          >
            <LeftArrow />
          </IconButton>
          <HeaderTitle>Usage and Reports</HeaderTitle>
        </ContentHeader>
      )}
      <ContentInner>
        <ContentWrapper sx={ContentWrapperSX}>
          {!waStatus && !loadingStatus && <OffStatus />}
          {waStatus && <LiveStatus loadingStatus={loadingStatus} />}
          {waStatus && <Utilization loadingStatus={loadingStatus} />}
          {waStatus && <UtilizationReport loadingStatus={loadingStatus} />}
        </ContentWrapper>
      </ContentInner>
    </Fragment>
  );
};

export default WAUsageReports;
