import { AuthenticatedReq } from "Utils/Axios";

export default class WAUsageApi {
  public static getWaStatus = async () => {
    return await AuthenticatedReq(`/platforms/wa/status`, "GET");
  };

  public static getWaHealthReport = async () => {
    return await AuthenticatedReq(`/whatsapp-usage/health-report`, "GET");
  };

  public static getWaMonthlyReport = async () => {
    return await AuthenticatedReq(`/whatsapp-usage/monthly-utilization`, "GET");
  };

  public static downloadWaMonthWiseReport = async (data: any) => {
    return await AuthenticatedReq(
      `/whatsapp-usage/download-consumed-report`,
      "POST",
      {
        data,
        responseType: "blob",
      }
    );
  };

  public static getRangeUtilization = async (from: Date, to: Date) => {
    return await AuthenticatedReq(
      `/whatsapp-usage/utilization?from=${from.getTime()}&to=${to.getTime()}`,
      "GET"
    );
  };
}
